<template>
  <Card shadow>
    <template #title>
      <Avatar size="small" icon="md-send" style="color: rgb(82, 196, 26); background: rgb(246, 255, 237)" />
      <span class="ivu-pl-8">更新日志</span>
    </template>
    <article class="version-doc">
      <Timeline>
        <TimelineItem v-for="(item, index) in versionLog" :color="item.color" :key="`item-${index}`">
          <template #dot>
            <Icon :type="item.icon" />
          </template>
          <span>{{ item.title }}</span>
          <p>
            <code>{{ item.date }}</code>
          </p>
          <ul>
            <li v-for="(li, i) in item.list" :key="`li-${i}`" v-html="li"></li>
          </ul>
        </TimelineItem>
      </Timeline>
    </article>
  </Card>
</template>

<script>
  import { Timeline, TimelineItem } from 'view-ui-plus'
  import versionLog from './version'

  export default {
    name: 'VersionLog',
    components: { Timeline, TimelineItem },
    data() {
      return {
        versionLog
      }
    }
  }
</script>
<style lang="less">
  .version-doc {
    .ivu-timeline .ivu-timeline-item-content {
      top: -6px;

      & > span {
        font-size: 16px;
      }
    }

    p {
      margin: 5px;
      font-size: 14px;
    }

    ul {
      padding-left: 22px !important;
    }

    li:not([class^='ivu-']) {
      margin-bottom: 5px;
      font-size: 14px;
    }

    code {
      padding: 0.2em 0.4em;
      background-color: #fff5f5;
      border-radius: 3px;
      color: #fa795e;
    }
  }
</style>
